import {useSelector} from "react-redux";
import {
    Button,
    Container,
    Grid,
    ListItem,
    ListItemText,
    Paper,
    Typography,
    Tabs,
    Tab, IconButton,
} from "@mui/material";

import Web3 from "web3";

import {useState} from "react";

import CoineusLoading from "../components/CoineusLoading";
import TokenListItem from "../components/TokenListItem";
import {CoineusUSDFormat} from "../utils/currency_format";
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';

import bbks_abi from "../abis/bbk_abi.json";
import useTokenList from "../hooks/useTokenList";

const holdBBKSAmount = 100;
const BBKS_CONTRACT = "0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25";

export default function HomePage() {

    const {wallet} = useSelector(state => state.coineus);

    const tokenList = useTokenList();

    const [pending, setPending] = useState(false);
    const [chainTab, setChainTab] = useState('bnb');

    const claimBBKSRewards = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(bbks_abi, BBKS_CONTRACT);

            contract.methods
                .claimRewards()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    // getUserInfo();
                    setPending(false);
                });
        }
    }

    return (<Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <Grid item xs={12}>

                    {
                        wallet.address && <Paper style={{marginTop: 10}}>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusUSDFormat(
                                        wallet.totals.bnb +
                                        wallet.totals.eth
                                    )}
                                    secondary="Total Wallet Value"
                                    primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                                    secondaryTypographyProps={{textAlign: 'right'}}
                                />
                            </ListItem>
                        </Paper>
                    }

                    <Tabs
                        variant="fullWidth"
                        value={chainTab}
                        onChange={(ev, val) => {
                            setChainTab(val)
                        }}
                    >
                        <Tab id="bnb" value="bnb" label="BNB"/>
                        <Tab id="eth" value="eth" label="ETH"/>
                    </Tabs>

                    {
                        chainTab === 'bnb' && <>
                            {
                                wallet.address && <Paper style={{marginTop: 10}}>
                                    <ListItem>
                                        <ListItemText
                                            primary={CoineusUSDFormat(wallet.totals.bnb)}
                                            secondary="Total BNB Value"
                                            primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </Paper>
                            }

                            {/*BNB*/}
                            <TokenListItem
                                contract_address="native"
                                network="bnb"
                                name="BNB"
                                native
                                price={wallet.prices.bnb?.native}
                                wrap="0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c"
                            />

                            {/*SRG*/}
                            <TokenListItem
                                contract_address="0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"
                                name="SRG"
                                network="bnb"
                                price={wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]}
                            />

                            <HoldBBKS/>

                            {
                                (!wallet.address || wallet.balances.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"] >= holdBBKSAmount) && tokenList.bnb.map(token => {
                                    return <TokenListItem
                                        key={token.contract_address}
                                        contract_address={token.contract_address}
                                        name={token.symbol}
                                        network="bnb"
                                        price={wallet.prices.bnb?.[token.contract_address]}
                                    />
                                })
                            }

                        </>
                    }


                    {
                        chainTab === 'eth' && <>
                            {
                                wallet.address && <Paper style={{marginTop: 10}}>
                                    <ListItem>
                                        <ListItemText
                                            primary={CoineusUSDFormat(wallet.totals.eth)}
                                            secondary="Total ETH Value"
                                            primaryTypographyProps={{fontWeight: 700, fontSize: '1.5rem', textAlign: 'right'}}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </Paper>
                            }

                            {/*ETH*/}
                            <TokenListItem
                                contract_address="native"
                                network="eth"
                                name="ETH"
                                native
                                price={wallet.prices.eth?.native}
                                wrap="0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2"
                            />

                            {/*SRG*/}
                            <TokenListItem
                                contract_address="0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"
                                name="SRG"
                                network="eth"
                                price={wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]}
                            />

                            {/*BBKS*/}
                            <TokenListItem
                                contract_address="0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"
                                name="BBKS"
                                network="eth"
                                price={wallet.prices.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]}
                                rewardsSymbol="SRG"
                                rewards={wallet.info.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.pending_rewards}
                                rewardsUSD={wallet.info.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"]?.pending_rewards * wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]?.inUSD}
                                rewardsClaim={<IconButton
                                    color="primary"
                                    size="small"
                                    onClick={claimBBKSRewards}
                                    disabled={wallet.chain !== 1}>
                                    <CurrencyExchangeIcon fontSize="small"/>
                                </IconButton>}

                            />

                            <HoldBBKS/>

                            {
                                (!wallet.address || wallet.balances.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"] >= holdBBKSAmount) && tokenList.eth.map(token => {
                                    return <TokenListItem
                                        key={token.contract_address}
                                        contract_address={token.contract_address}
                                        name={token.symbol}
                                        network="eth"
                                        price={wallet.prices.eth?.[token.contract_address]}
                                    />
                                })
                            }
                        </>
                    }
                </Grid>
            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>

        </Container>
    );
}

function HoldBBKS() {

    const {wallet} = useSelector(state => state.coineus);

    if (wallet.address && wallet.balances.eth?.["0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25"] < holdBBKSAmount) {
        return <Grid item xs={12}>
            <Paper style={{padding: 20, border: '1px solid #723DF4', textAlign: 'center'}}>
                <Typography style={{marginBottom: 10}}>To unlock other SRG20 Tokens,<br/> you need to
                    hold <strong>{holdBBKSAmount} BBKS</strong></Typography>
                <Button variant="contained" href="https://swap.surgeprotocol.io" target="_blank">Buy BBKS</Button>
            </Paper>
        </Grid>
    }

    return <></>

}