import store from "../redux/store";
import Web3 from "web3";
import {makeBatchRequest} from "./promisify";
import {updateBalances, updateInfo, updateTotals} from "../redux/slices/coineusSlice";
import coineus_abi from "../abis/coineus.json";
import bbks_abi from "../abis/bbk_abi.json";
import IERC20_ABI from "../abis/IERC20.json";
import axios from "axios";

export default function getUserBalances(wallet_address) {
    const _store = store.getState();
    const {rpc} = _store.coineus;

    if (wallet_address === undefined) {
        return;
    }

    let calls = [];
    calls.push(axios.get('https://coineus.app/assets/apps/krakentracker/srg20_bnb.json'));
    calls.push(axios.get('https://coineus.app/assets/apps/krakentracker/srg20_eth.json'));

    Promise.all(calls).then(resp => {
        getBNB(rpc.bnb, wallet_address, resp[0].data.tokens);
        getETH(rpc.eth, wallet_address, resp[1].data.tokens);
    })
}

function getBNB(rpc, wallet_address, tokenList) {

    const web3BSC = new Web3(rpc);

    const srg_contract = new web3BSC.eth.Contract(IERC20_ABI, '0x9f19c8e321bD14345b797d43E01f0eED030F5Bff');


    const callsBSC = [
        web3BSC.eth.getBalance(wallet_address),
        srg_contract.methods.balanceOf(wallet_address).call,
    ];

    tokenList.map(token => {
        const contract = new web3BSC.eth.Contract(IERC20_ABI, token.contract_address);
        callsBSC.push(contract.methods.balanceOf(wallet_address).call);
        return token;
    })

    makeBatchRequest(web3BSC, callsBSC).then(resp => {
        let [
            bnb_balance,
            srg_balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: 'native',
            balance: bnb_balance / (10 ** 18)
        }));

        store.dispatch(updateBalances({
            chain: 'bnb',
            contract_address: '0x9f19c8e321bD14345b797d43E01f0eED030F5Bff',//srg
            balance: srg_balance / (10 ** 9)
        }));

        tokenList.map((token,i) => {
            store.dispatch(updateBalances({
                chain: 'bnb',
                contract_address: token.contract_address,//srg
                balance: resp[i + 2] / (10 ** token.decimals)
            }));
            return token;
        })

        store.dispatch(updateTotals('bnb'));

    })
}

function getETH(rpc, wallet_address, tokenList) {

    const web3ETH = new Web3(rpc);
    const bbks_contract = new web3ETH.eth.Contract(bbks_abi, '0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25');
    const srg_contract = new web3ETH.eth.Contract(coineus_abi, '0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe');

    const callsETH = [
        web3ETH.eth.getBalance(wallet_address),
        bbks_contract.methods.balanceOf(wallet_address).call,
        bbks_contract.methods.checkPendingRewards(wallet_address).call,
        srg_contract.methods.balanceOf(wallet_address).call,
    ];

    tokenList.map(token => {
        const contract = new web3ETH.eth.Contract(IERC20_ABI, token.contract_address);
        callsETH.push(contract.methods.balanceOf(wallet_address).call);
        return token;
    })

    makeBatchRequest(web3ETH, callsETH).then(resp => {
        let [
            eth_balance,
            bbks_balance,
            bbks_rewards,
            srg_balance,
        ] = resp;

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: 'native',
            balance: eth_balance / (10 ** 18)
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25',//bbk
            balance: bbks_balance / (10 ** 9)
        }))

        store.dispatch(updateInfo({
            chain: 'eth',
            contract_address: '0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25',//bbk
            info: {
                pending_rewards: bbks_rewards / (10 ** 9)
            }
        }))

        store.dispatch(updateBalances({
            chain: 'eth',
            contract_address: '0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe',//srg
            balance: srg_balance / (10 ** 9)
        }))

        tokenList.map((token,i) => {
            store.dispatch(updateBalances({
                chain: 'eth',
                contract_address: token.contract_address,//srg
                balance: resp[i + 4] / (10 ** token.decimals)
            }));
            return token;
        })

        store.dispatch(updateTotals('eth'));

    })
}