import {useEffect, useState} from "react";
import axios from "axios";

export default function useTokenList() {

    const [tokenList, setTokenList] = useState({
        bnb: [],
        eth: []
    });

    useEffect(() => {
        let calls = [];
        calls.push(axios.get('https://coineus.app/assets/apps/krakentracker/srg20_bnb.json'));
        calls.push(axios.get('https://coineus.app/assets/apps/krakentracker/srg20_eth.json'));

        Promise.all(calls).then(resp => {
            setTokenList({
                bnb: resp[0].data.tokens,
                eth: resp[1].data.tokens
            })
        })
    }, [])

    return tokenList;

}