import {Container, Grid, Paper} from "@mui/material";
import BurnSupply from "../components/BurntSupply";

import Web3 from "web3";
import coineus_abi from "../abis/coineus.json";
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import axios from "axios";

import {
    LineSeries,
    FlexibleWidthXYPlot,
    Crosshair
} from 'react-vis';
import '../../node_modules/react-vis/dist/style.css';

const web3 = new Web3("https://cloudflare-eth.com/");
const bbks_contract = new web3.eth.Contract(coineus_abi, "0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25");

const calls = [
    bbks_contract.methods.totalSupply().call,
    bbks_contract.methods.balanceOf('0x000000000000000000000000000000000000dEaD').call
];

export default function Stats() {
    useEffect(() => {
        axios.post(
            "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v1-eth",
            {
                query: `
            {
              token(id:"0xf654d4c3cc334324ad474a0d5d3708dca4c1cb25") {
                tokenDayData {
                  date
                  dailyTxns
                  dailyVolumeUSD
                  priceUSD
                }
              }
            }
            `
            }
        ).then(resp => {
            const data = resp.data.data.token.tokenDayData;
            const chart_data = data.map(d => {
                return {x: new Date(d.date * 1000), y: parseFloat(d.priceUSD.substr(0, 6))}
            });
            console.log(chart_data)
            setChartData(chart_data)
        })
    }, [])

    const [supply, setSupply] = useState(0);
    const [burned, setBurned] = useState(0);
    const [chartData, setChartData] = useState(undefined);
    const [crosshairValues, setCrosshairValues] = useState([]);

    const onMouseLeave = () => {
        setCrosshairValues([])
    };


    const onNearestX = (value, {index}) => {
        setCrosshairValues([{x: chartData[index].x, y: chartData[index].y}]);
    };


    useEffect(() => {
        makeBatchRequest(web3, calls).then(resp => {
            let [
                bbks_supply,
                bbks_burned
            ] = resp;

            setSupply(bbks_supply / (10 ** 9));
            setBurned(bbks_burned / (10 ** 9));
        })
    }, [])

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Grid item xs={12}>
                    <Paper style={{padding: 15}}>
                        <FlexibleWidthXYPlot
                            height={200}
                            strokeWidth={3}
                            margin={{left: 0, right: 0, top: 0, bottom: 0}}
                            onMouseLeave={onMouseLeave}
                        >
                            <LineSeries color="#723DF4" fill="transparent" data={chartData} onNearestX={onNearestX}/>
                            <Crosshair
                                values={crosshairValues}
                                className={'test-class-name'}
                                style={{
                                    line:{background: "#fff"}
                                }}
                                itemsFormat={(values) => {
                                    return values.map((v, i) => {
                                        return {
                                            title: "Price",
                                            value: v.y
                                        };
                                    });
                                }}
                                titleFormat={(values) => {
                                    return {
                                        title: "Date",
                                        value: values[0].x.toLocaleDateString()
                                    };
                                }}
                            />
                        </FlexibleWidthXYPlot>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <BurnSupply
                        supply={supply}
                        burned={burned}
                    />
                </Grid>
            </Grid>
        </Container>
    )

}